
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
body {
  line-height: 1;
}
header,footer,body,nav{
  display: block;
}
ol, ul {
     list-style: none;
}
body {
  background: #f0f2f5;
  font-family: "Helvetica Neue",Helvetica,Arial,"Hiragino Sans GB","Hiragino Sans GB W3","Microsoft YaHei UI","Microsoft YaHei","WenQuanYi Micro Hei",sans-serif;
  font-size: 14px;
  color: #444;
}
.layout-head {
  /*background: rgba(193, 193, 193, 0.06);*/
  background: rgba(234, 234, 241, 0.8);
  color: #1e1e1e;
  height: 90px;
  line-height: 90px;
  width: 100%;
  margin: 10px 10px;
  z-index: 10;
}
.layout-head-inner{
  width: 1200px;
  margin: 0 auto;
}
.layout-head-inner img {
  height:90px;
  width:90px;
  float: left;
}
.head-inner-nav {
  float: right;
}
.head-inner-nav ul {
  overflow: hidden;
}
.head-inner-nav li {
  cursor: pointer;
  float: left;
}
.nav-pile {
  padding: 0 10px;
}
.layout-body{
  width: 100%;
  /*margin-top:90px ;*/
}
.layout-foot{
  text-align: center;
  height: 80px;
  width: 100%;
  line-height: 80px;
  background: #e3e4e8;
  clear: both;
  margin: 10px 10px;
}
.button {
  background: rgba(128,128,128 ,0.6);
  color: rgba(30, 30, 30, 0.8);
  display: inline-block;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 15px;
}
.g-form {
}
.g-form-line {
  padding: 15px 0;
}
.g-form-label {
  width: 100px;
  font-size: 16px;
  display: inline-block;
}
.g-form-input {
  display: inline-block;
}
.g-form-input input {
  height: 30px;
  width: 150px;
  line-height: 30px;
  vertical-align: middle;
  padding: 0 10px;
  border: 1px solid #ccc;
}
.g-form-btn {
  padding-left: 100px;
  width:100px;
}
.g-form-error {
  color: red;
  padding-left: 5px;
  font-size: 8px;
}

.dialog-wrap {
  position: fixed;
  width: 100%;
  height: 100%;
}
.dialog-cover {
  background: #000;
  opacity: .3;
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.dialog-content {
  width: 400px;
  position: fixed;
  max-height: 50%;
  overflow: auto;
  background: rgba(206, 206, 225, 0.6);
  top: 20%;
  left: 60%;
  margin-left: -25%;
  z-index: 10;
  border: 2px solid rgb(208, 208, 225);
  padding: 2%;
  line-height: 1.6;
  font-family: "Times New Roman";
  font-size: 18px;
  font-weight: 600;
}
.dialog-close {
  position: absolute;
  right: 2px;
  top: 0px;
  width: 20px;
  height: 20px;
  margin: 0px 1px;
  text-align: center;
  cursor: pointer;
  font-size:24px;
  font-weight: 800;
}
.dialog-close:hover {
  color: rgba(128,0,0 ,1);
}


































































































