.slide-trans-enter-active[data-v-3980ecfd] {
  -webkit-transition: all .5s;
  transition: all .5s;
}
.slide-trans-enter[data-v-3980ecfd] {
  -webkit-transform: translateX(1000px);
          transform: translateX(1000px);
}
.slide-trans-old-leave-active[data-v-3980ecfd] {
  -webkit-transition: all .5s;
  transition: all .5s;
  -webkit-transform: translateX(-1000px);
          transform: translateX(-1000px);
}
.slide-show[data-v-3980ecfd] {
  position: relative;
  margin: 15px 15px 15px 0;
  width: 100%;
  height: 400px;
  overflow: hidden;
}
.slide-show h2[data-v-3980ecfd] {
  position: absolute;
  width: 1200px;
  height: 100%;
  color: #000000;
  background: rgba(128,128,128 ,0.5);
  opacity: .5;
  bottom: 0;
  height: 30px;
  text-align: left;
  padding-left: 15px;
  padding-top: 10px;
}
.slide-img[data-v-3980ecfd] {
  width: 100%;
}
.slide-img img[data-v-3980ecfd] {
  width: 100%;
  position: absolute;
  top: 0;
}
.slide-pages[data-v-3980ecfd] {
  position: absolute;
  bottom: 10px;
  right: 15px;
}
.slide-pages li[data-v-3980ecfd] {
  display: inline-block;
  padding: 0 10px;
  cursor: pointer;
  color: #000000;
}
.slide-pages li .on[data-v-3980ecfd] {
  text-decoration: underline;
}



























































































